.notice {
  @include material-card;
  position: relative;
  margin: 1em 0;
  padding: 0 0.75em;
  overflow: auto;

  .notice-title {
    position: relative;
    font-weight: 700;
    margin: 0 -0.75em;
    padding: 0.2em 3em;
    border-bottom: 1px solid var(--bg-color);

    i {
      position: absolute;
      top: 0.8em;
      left: 1.8em;
      transform: translate(-50%, -50%);
    }
  }

  .notice-content {
    display: block;
    margin: 0.5em 0.5em;
  }
}

.notice.note {
  background-color: $bg-color-notice-note-content;

  .notice-title {
    background-color: $bg-color-notice-note-title;

    i {
      color: $fg-color-notice-note-icon;
    }
  }
}

