.navigation {
  @include shadow;
  background-color: var(--card-color-2);
  position: fixed;
  transition: top 300ms 300ms cubic-bezier(0.4, 0, 0.2, 1);
  top: 0;
  z-index: 100;
  height: 3em;
  width: 100%;   

  a, span {
    @include text-shadow;
    display: inline;
    font-size: $font-size-large;
    font-family: $font-family;
    font-weight: 600;
    line-height: 2;
    color: rgba(var(--font-color), $text-full);
  }

  a {    
    @include hover-text;
  }

  .navigation-title {
    letter-spacing: 0.1rem;
    text-transform: uppercase;
  }

  .navigation-list {
    float: right;
    list-style: none;
    margin-bottom: 0;
    margin-top: 0;

    .navigation-item {
      float: left;
      margin: 0;
      position: relative;

      a, span {
        margin-left: 1em;
      }
    }
  }

  i {
    cursor: pointer;
    @include hover-text;
  }
}
