*,
*:after,
*:before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  overflow-y: scroll;
}

body {
  color: rgba(var(--font-color), $text-high);
  background-color: var(--bg-color);
  font-family: $font-family;
  font-size: $font-size-normal;
  font-weight: 400;
  line-height: 1.5;
  
  @media only screen and (max-width: $screen-break-width) {
    font-size: ($font-size-normal * $font-size-break-offset);
  }
}

a {
  @include word-wrap;

  font-weight: 400;
  color: var(--accent-color);
  text-decoration: none;
  transition: all 0.25s ease-in;

  @media (hover: hover) and (pointer: fine) {
    &:focus, &:hover {
      text-decoration: underline;
    }
  }
}

h1, h2, h3, h4, h5, h6 {
  @include text-shadow;
  line-height: 1.25;
  font-family: $font-family;
  font-weight: 600;
  color: rgba(var(--font-color), $text-full);
  margin-top: 1.5em;
  overflow: visible;

  @media (hover: hover) and (pointer: fine) {
    &:hover .heading-link {
      visibility: visible;
    }
  }

  .heading-link {
    color: var(--accent-color);
    font-weight: inherit;
    text-decoration: none;
    visibility: hidden;
  }

  .title-link {
    color: inherit;
    font-weight: inherit;
    text-decoration: none;
  }
}

h1 {
  font-size: $font-size-h1;
}

h2 {
  font-size: $font-size-h2;
}

h3 {
  font-size: $font-size-h3;
}

h4 {
  font-size: $font-size-h4;
}

h5 {
  font-size: $font-size-h5;
}

h6 {
  font-size: $font-size-h6;
}

b,
strong {
  font-weight: 700;
}

pre {
  @include material-card;
  display: block;
  font-family: $code-font-family;
  font-size: $font-size-small;
  font-weight: 400;
  overflow-x: auto;
  padding: 0.8em;
  background-color: var(--card-color-2);

  code {
    display: inline-block;
    background-color: inherit;
    color: inherit;
  }
}

code {
  @include word-wrap;
  font-family: $code-font-family;
  font-size: $font-size-small;
  font-weight: 400;
  background-color: var(--card-highlight);
  color: rgba(var(--font-color), $text-high);
  padding: .2em .4em;
  border-radius: $border-radius;
}

blockquote {
  border-left: 2px solid var(--card-highlight);
  padding-left: 2rem;
  font-weight: 400;
  font-style: italic;
}

th, td {
  padding: 0.5em;
  text-align: center;
}

table {
  border: 4px solid var(--card-color-3);
  border-collapse: collapse;
}

table td,
table th {
  border: 2px solid var(--card-color-3);
}

table tr:first-child th {
  border-top: 0;
}

table tr:last-child td {
  border-bottom: 0;
}

table tr td:first-child,
table tr th:first-child {
  border-left: 0;
}

table tr td:last-child,
table tr th:last-child {
  border-right: 0;
}

img {
  max-width: 100%;
  height: auto;
}

figure {
  text-align: center;
}

.preload-transitions * {
  $null-transition: none !important;

  -webkit-transition: $null-transition;
  -moz-transition: $null-transition;
  -ms-transition: $null-transition;
  -o-transition: $null-transition;
  transition: $null-transition;
}

.wrapper {  
  position: absolute;
  display: flex;
  flex-direction: column;

  min-height: 99%;
  width: 100%;
}

.container {
  margin: 0 auto;
  max-width: $screen-main-width;
  width: 100%;
  padding-left: 1em;
  padding-right: 1rem;
}

.fab {
  font-weight: 400;
}

.fas {
  font-weight: 700;
}

.float-right {
  float: right;
}

.float-left {
  float: left;
}

img.emoji {
  height: 1em;
  width: 1em;
  margin: 0 0.05em 0 0.1em;
  vertical-align: -0.1em;
}

.tag-link:before {
  content: "#";
}

.inline-svg {
  display: inline-flex;
  align-self: center;
}
.inline-svg svg {
  font-size: inherit;
  height: 1em;
  width: 1em;
  top: .125em;
  position: relative;
}