.resume {

  h4, h5, h6{
    padding-top: 4rem;
    margin: -4rem 0 0 0;
  }

  h3 + p, h4 + p {
    margin: 0 0 0 0;
  }

  h1, p + ul, h3 + ul, h4 + ul {
    margin: 0 0 2rem 0;
  }

  h2 {
    font-weight: 900;
  }

  h3:before {
    @include sticky-header-before;
  }

  h3 {    
    @include sticky-header;
  }
}
