@mixin word-wrap() {
    overflow-wrap: break-word;
    word-wrap: break-word;
    -ms-word-break: break-all;
    word-break: break-all;
    word-break: break-word;
    -ms-hyphens: auto;
    -moz-hyphens: auto;
    -webkit-hyphens: auto;
    hyphens: auto;
}

@mixin sticky-header-before() {
    position: absolute;
    color: var(--accent-color);
    content: "#";
    font-weight: 700;
    left: -0em;
}

@mixin sticky-header() {
    position: sticky;
    top: -1px;
    padding: 0.25em 0 0.25em 0.8em;
    margin: 0 0 0 -2px;
    z-index: 50;
}

@mixin hover-text() {
    color: rgba(var(--font-color), $text-full);
    @media (hover: hover) and (pointer: fine) {
        &:hover, &:focus {
            color: var(--accent-color);
        }
    }
}

@mixin shadow() {
    box-shadow: 0 3px 6px $img-shadow-color, 0 3px 6px $img-shadow-color2;
}

@mixin text-shadow() {
    text-shadow: 1px 1px 1px $img-shadow-color2;
}

@mixin svg-shadow() {
    filter: drop-shadow(2px 2px 1px $img-shadow-color2);
    -webkit-filter: drop-shadow(2px 2px 1px $img-shadow-color2);
}

@mixin material-card() {
    @include shadow;
    border-radius: $border-radius;
    padding: 0.5em 1em 1em 1em;
    margin-bottom: 1em;
    background-color: var(--card-color);
    h1, h2, h3, h4, h5, h6 {
        background-color: var(--card-color);
    }
}
