.content {
  flex: 1;
  display: flex;  
  flex-flow: wrap;
  margin-top: 4em;
  margin-bottom: 2em;
  
  article {
    @include material-card;

    details {
      summary {
        cursor: pointer;
      }
    }

    header {
      margin-bottom: 2em;

      h1 {
        font-size: $font-size-title;
        margin: 0;

        @media only screen and (max-width: $screen-break-width) {
          font-size: ($font-size-title * $font-size-break-offset);
        }
      }
    }

    footer {
      margin-top: 4rem;
    }

    p {
      text-align: justify;
      text-justify: auto;
      hyphens: auto;
    }

    div {
      h2:before {
        @include sticky-header-before;
      }

      h2 {
        @include sticky-header;
      }

      h3, h4, h5, h6 {
        padding-top: 2em;
        margin-top: -2em;;
      }
    }      
  }

  .post-meta {
    color: rgba(var(--font-color), $text-low);
    i {
      padding-right: 0.3em;
    }

    span {
      display: inline-block;
    }
  }

  figure {
    margin: 0;
    padding: 0;
  }

  figcaption p {
    text-align: center;
    font-style: italic;
    font-size: $font-size-small;
    margin: 0;
  }

  .list {
    h1 {
      margin-top: 0;
    }
  }
}

img {
  @include shadow;
  border-radius: $border-radius;
}

.avatar img {
  height: auto;
  border-radius: 50%;
}

.postimg img {
  width: 100%;
  max-height: 16em;
  object-fit: cover;
  margin-top: 0.5em;
}

.summary {
  @include material-card;
  font-size: $font-size-normal;
  
  .date {
    display: inline-block;
    flex: 1;
    width: 20rem;
    text-align: right;
    margin-right: 3rem;

    @media only screen and (max-width: $screen-break-width) {
      display: block;
      text-align: left;
    }
  }

  .title {        
    @include text-shadow;
    @include hover-text;
    display: block;
    font-size: $font-size-h2;
    font-family: $font-family;
    font-weight: 700;
  }     
}

.list {
  ul {
    list-style: none;
    padding: 0;
  } 
}

.centered {
  display: flex;
  align-items: center;
  justify-content: center;

  .about {
    text-align: center;

    h1 {
      margin-top: 2rem;
      margin-bottom: 0.5rem;      
    }

    h2 {
      margin-top: 1rem;
      margin-bottom: 0.5rem;
    }

    ul {
      list-style: none;
      padding: 0;

      li {
        display: inline-block;
        position: relative;
        padding-left: 0.5em;
        padding-right: 0.5em;

        a {         
          @include hover-text;
          .inline-svg {
            font-size: $font-size-h1;
          }
          svg{
            @include svg-shadow;
          }
        }
      }
    }
  }

  .error {
    text-align: center;

    h1 {
      margin-top: 2rem;
      margin-bottom: 0.5rem;
      font-size: $font-size-title;
    }

    h2 {
      margin-top: 2rem;
      margin-bottom: 3.2rem;
      font-size: $font-size-h1;
    }
  }
}
