.pagination {
  margin-top: 6rem;
  text-align: center;
  font-size: $font-size-normal;

  li {    
    padding: 0 0.5em 0 0.5em;
    display: inline;
    text-align: center;
    font-weight: 700;

    span {
      margin: 0;
      text-align: center;
      width: 3.2rem;
    }

    a {
      font-weight: 400;

      span {
        margin: 0;
        text-align: center;
        width: 3.2rem;
      }
    }
  }
}
