.toc {  
  @include material-card;
  position: fixed;
  max-width: 18em;
  left: 1em;

  .toc-title {
    font-size: $font-size-large;
    text-transform: uppercase;
    margin: 0 0 0 0;
  }

  #TableOfContents {
    font-size: $font-size-small;
    width: 100%;

    ul {      
      padding-left: 1.5em;
      margin-bottom: 0;

      li::marker {
        content: "# ";
        color: var(--accent-color);
      }
    }
  }
}

@media only screen and (max-width: ($screen-main-width + (25em * 2))) {
  .toc {
    max-width: calc((100vw - 62em)/2);
  }
}

@media only screen and (max-width: ($screen-main-width + (21.5em * 2))) {
  .toc {
    display: none;
  }
}