.float-container {
  ul {  
    bottom: 0em;
    right: 0em;
    z-index: 100;
    position: fixed;
    list-style: none;
    padding: 0;
    margin-bottom: 1em;
    margin-right: 1em;

    li {
      position: relative;
      padding-left: 0.5em;
      padding-right: 0.5em;
      transition: all 0.25s ease-in;

      a {
        @include hover-text;
        color: rgba(var(--font-color), $text-low);
        
        .inline-svg {
          font-size: $font-size-h2;
          @media only screen and (max-width: $screen-break-width) {
            font-size: $font-size-h1;
          }
        }
        svg{
          @include svg-shadow;
        }
      }
    }
    
    #to-top {
      visibility: hidden;
      opacity: 0;
      transition: all 300ms;
    }
  }
}
